@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Avenir Next";
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

nav {
  padding: 60px 30px 0px 30px;
  flex: 1.5 1;
  flex: var(--sidebar-flex) 1;
  background: rgb(72, 82, 191);
}

.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  flex: 6 1;
  flex: var(--condiv-flex) 1;
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.profilepic {
  margin: 30px;
  border-radius: 50%;
}

nav ul {
  font-size: 1.6rem;
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}

nav ul li {
  margin: 40px;
}

a {
  text-decoration: none;
  color: white;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
  color: rgb(136, 139, 181);
}

.name-title {
  font-size: 50px;
  background: -webkit-linear-gradient(rgb(65, 79, 231), rgb(255, 161, 177));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
  margin-left: 8px;
  color: rgb(72, 82, 191);
  white-space: nowrap;
}

p {
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 10px;
}

h1 {
  color: rgb(72, 82, 191);
}

h3 {
  color: rgb(109, 118, 221);
  font-size: 16pt;
}

h4 {
  color: rgb(136, 139, 181);
  font-weight: bold;
  font-style: italic;
}

h5 {
  color: rgb(136, 139, 181);
  font-size: 10pt;
}

.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 30px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover
{
  color: #ffffff;
}

.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}

.active {
  font-weight: bolder;
}

.skills ul li {
  list-style-type: none;
  margin: 30px;
  border-bottom: 2px solid rgb(139, 147, 243);
  padding-bottom: 3%;
  width: 40%;
}
 
.skills ul li:last-child {
  border: none;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }

  .App {
    flex-direction: column;
  }

  .sidebar {
    padding: 30px;
    background: rgb(72, 82, 191);
  }

  .condiv {
    padding: 50px;
  }

  nav ul {
    display: flex;
  }

  nav ul li {
    margin: 10px;
  }

  .social {
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }

  .social i {
    padding-right: 0px;
  }
}

